import React, { useEffect, useState } from 'react';
import { n } from './helpers.jsx';
import { AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';
import _ from "lodash"

export default function GlobalTvl(price) {
  let dotPrice = price.price.dotPrice;
  let hDot;


  const [tvl            , setTvl             ] = useState();
  const [dailyVar       , setDailyVar        ] = useState();  
  
  const getData = async () => {
    const res = await fetch("https://auction-service-prod.parallel.fi/crowdloan/statistics/polkadot?fundIds=2000-1muqpuFcWvy1Q3tf9Tek882A6ngz46bWPsV6sWiYccnVjKb-0,2002-14e4GmLj5CccWe9Rant9q6yQro1oysqvKiBiHcpCRoscZ1yY-0,2003-1EdsnniYSKNjHNAvDgvBfRNzKnSzi6kgsHQFCG4PhAyyJWH-0,2004-12KHAurRWMFJyxU57S9pQerHsKLCwvWKM1d3dKZVx7gSfkFJ-1,2006-1j5YyEGdcPd9BxkzVNNjKkqdi5f7g3Dd7JMgaGUhsMrZ6dZ-0,2012-15kjdKF4hRbYWzLjovPiohT5pVheXhhk8oKHr3DyTaxF2evd-0,2013-152deMvsN7wxMbSmdApsds6LWNNNGgsJ8TTpZLTD2ipEHNg3-0,2015-1k9St8FDkcTVSe6HzSRQivYi3qgRuQfQp8btpVMrdTFs99S-0,2017-16RzEcgXVzXXn2gEQbqqp74Pw7MJSb7PKtz29BhVZmpXBKRn-0,2018-13n7S6wAFjSqppxyjfSxcPNqQv5jL7PYtxjg3Gxw3RALpyBk-0,2021-12LxQoLA9hebiMSPT3G7ixz73LLuYomMNuRLqX7c9bRWxDFG-0,2028-13pQt6LnK2tXZtXbiQ6PBYikEoNTi6MXkeBdQCeyR9hm6k1p-0");
    const responseJson = await res.json();
    const getDotPrice   = await fetch('https://min-api.cryptocompare.com/data/v2/histoday?fsym=DOT&tsym=USD&limit='+Object.keys(responseJson.stats).length)
    const finalDotPrice = await getDotPrice.json()
      hDot = finalDotPrice.Data.Data;

      let dates = responseJson.stats.map(function(date, i){
        return {
          dates: date.date.replace(', 2021', '')
        }
      });
      
      
      let newArray = responseJson.stats.map(function(item, i){
        delete item.date;
        return item
      });
      
      let tvlFinal = newArray.map(function(item, i){
        return {
          dots : _.sum(_.values(newArray[i])),
          value: _.sum(_.values(newArray[i]))*hDot[i].close
        }
        
      })
      
      let finalfinal = _.merge(dates, tvlFinal);

      setTvl(finalfinal)
      setDailyVar((((tvlFinal[tvlFinal.length - 1].value - tvlFinal[tvlFinal.length - 2].value)/tvlFinal[tvlFinal.length - 1].value)*100).toFixed(2))
  }
  useEffect(()=>{
    getData()
  }, [])
  

  const renderLegend = (props) => {
    const { payload } = props;
    return (
      <ul className="tvl-tooltip"> 
        {
          payload.map((entry, index) => (
            <li key={`item-${index}`}>
              <p><strong>{entry.payload.dates}</strong></p>
              <p>
                <strong>{n(entry.payload.dots)} DOT </strong>
              </p>
              <p className="small-gray">
                $ { n(entry.payload.value) }

              </p>
            </li>
          ))
        }
      </ul>
    );
  }



  return hDot ? 'loading' : (
    <>

      <p className="big"> 
       
        {!tvl ? "" :  '$'+ n(parseFloat(tvl[tvl.length - 1].value))} 
        {dailyVar  &&
          <span className={ dailyVar.includes("-") ? 'negative variation': 'positive variation' }>
              { dailyVar.includes("-") ? '' + dailyVar+ '%' : '+' + dailyVar+ '%' }
          </span>
         }
      </p>
      
      {tvl &&
        <>
          <div className="globalTvl">
            <ResponsiveContainer className="crowdloantvl" width="100%" height={250} debounce={1}>
              <AreaChart
                width="100%"
                height={250}
                data={tvl}
                stroke={3}
                margin={{
                  top: 10,
                  right: 0,
                  left: 30,
                  bottom: 0,
                }}
              >

                <defs>
                  <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
                    <stop offset="5.23%" stopColor="#0C3EE3" stopOpacity={1}/>
                    <stop offset="7.34%" stopColor="#0C3EE3" stopOpacity={1}/>
                    <stop offset="97.88%" stopColor="rgba(255, 255, 255, 0.64)" stopOpacity={1}/>
                    
                  </linearGradient>
                </defs>

                <CartesianGrid strokeDasharray="1 5" />
                <XAxis dataKey="dates" hide={true}/>
                
                <Tooltip content={renderLegend}  />
                <Area scaleToFit={true} type="monotone" dataKey="value" strokeWidth="2.5" stroke="#0C3EE3" fill="url(#colorUv)" />
                <YAxis orientation="right" tickFormatter={(value) => new Intl.NumberFormat('en', { notation: "compact" , compactDisplay: "short" }).format(value)} />
              </AreaChart>
            </ResponsiveContainer>
          </div>
        </>
      }

    </>
  )
}

