import React, { useEffect, useState } from 'react';
import Header         from './components/header'
import CrowdloanList  from './components/crowdloanList'
import _ from "lodash"

import './styles/styles.css'; 

function App() {
  const raiseApi          = "https://auction-service-prod.parallel.fi/crowdloan/polkadot"
  const contributionsApi  = "https://auction-service-prod.parallel.fi/crowdloan/polkadot/contributions"
  const tvlApi            = "https://auction-service-prod.parallel.fi/crowdloan/total-value"
  const BinanceApi        = "https://www.binance.com/bapi/earn/v1/public/lending/dot/project/home/list?"
  const FilterByDotAmount = "http://localhost:3000/crowdloan/total-contribution-by-Dot"
  const historicalTvlAPI     = "https://auction-service-prod.parallel.fi/crowdloan/historical-tvl"

  const [raised        , setRaised        ] = useState();
  const [contributions , setContributions ] = useState();
  const [tvl           , setTvl           ] = useState();
  const [DotAmount     , setDotAmount     ] = useState();
  const [historicalTvl , setHistoricalTvl ] = useState();
  const [binance       , setBinance       ] = useState();

  const getRaised = async () => {
    const res = await fetch(raiseApi);
    const responseJson = await res.json();

    setRaised(responseJson)
  }

  const getContributions = async () => {
    const res = await fetch(contributionsApi);
    const responseJson = await res.json();

    setContributions(responseJson)

  }

  const getTvl = async () => {
    const res = await fetch(tvlApi);
    const responseJson = await res.json();
    setTvl(responseJson)
  }

  const getDotamount = async () => {
    const res = await fetch(FilterByDotAmount);
    const responseJson = await res.json();
    setDotAmount(responseJson)
  }

  const getHistoricalTvl = async () => {
    const res = await fetch(historicalTvlAPI);
    const responseJson = await res.json();
    setHistoricalTvl(responseJson)
  }

  const getBinance = async () => {
    const res = await fetch(BinanceApi+"auctioning=true");
    const responseJson = await res.json();
    const res2 = await fetch(BinanceApi+"auctioning=false");
    const responseJson2 = await res2.json();

    setBinance([responseJson, responseJson2])
  }


  useEffect(()=>{
    function load() {
      getRaised();
      getContributions();
      getTvl();
      getBinance();
      getDotamount();
      getHistoricalTvl();
    }

    load();


      setInterval(() => {
        load()
      }, 30000)
  
  }, [])

  return (
    <>
      <div className="container">
        <Header />
        { raised &&
          <CrowdloanList raise={raised} contributions={contributions} tvl={tvl} htvl={historicalTvl} other={binance}  dotAmount={DotAmount} />
        }
      </div>
    </>
  );
}

export default App;
