import React from 'react';
import Parachains from '../../data/parachains.jsx';
import { m, Porcent } from '../../data/helpers.jsx';
import Logo from '../../components/Logo';

export default function Dotmarket(data) {
  const raising       = data.raise;
  const parachain     = Parachains.parachain;
  const aucitonShare  = data.marketShare;
  const supply        = '1103303471';
  return (
    <>
        <div>
          <div className="network-header flex justify">
            <h4 className="network-name">PROJECT</h4>
            <h4 className="network-cap">DOT</h4>
            <h4 className="network-auction">% Auction</h4>
            <h4 className="network-total">% Total</h4>
          </div>
          <div>
            { 
              !raising ? "Getting Data" : 
              raising.sort((a, b) => b.raised - a.raised).filter(data => data.paraId !== "2017" && data.paraId !== "2003" && data.paraId !== "2021" && data.paraId !== "2026" ).map((index) => {
              
              const id = index.paraId,
                    r  = index.raised,
                    t  = raising[0].raised;
              return (
                <>
                  <div key={id} className="network flex justify">
                    {!parachain ? 'Loading...' : parachain.filter(i => i.paraid === id).map(network => (
                      <div  className="network-name"><Logo src={network}/> <span> {network.name} </span></div>
                    ))}    
                    
                    <p className="network-cap">{m(r)}</p>
                    <p className="network-auction">{ Porcent(r/aucitonShare) + '%'}</p>
                    <p className="network-total">{ Porcent((r/10000000000)/supply) + '%'}</p>
                  </div>
                </>
              )
            })
          }

          </div>
        </div>
    </>
  )
}