import React from 'react';
import { n, m , Porcent } from '../../data/helpers.jsx';
import Binance from '../../assets/images/binance.svg';
import Networks from '../../assets/images/network.svg';
import Parallel from '../../assets/images/parallel.svg';
import { PieChart, Pie, Tooltip, Cell, ResponsiveContainer } from 'recharts';




export default function MarketDominance(data) {
  const binanceTVL = data?.data;
  const decimals   = 10000000000;
  const share      = data?.share?.share;
  const market     = data?.share.market/decimals; 
  let bin = false;
  let bin2 = false;
  let datachart = false;
  let BinancePortcent = false;
  let paralelPorcent  = false;
  let nonshare = false;
  let otherMarkets = false;
  if(binanceTVL) {
    
  bin = binanceTVL[0].data.reduce(function(sum, current) {
    return parseInt(sum) + parseInt(current.votedAmount);
  }, 0);
  bin2 = binanceTVL[1].data.reduce(function(sum, current) {
    return parseInt(sum) + parseInt(current.votedAmount);
  }, 0);


  BinancePortcent = Porcent((bin+bin2)/market);
  paralelPorcent  = Porcent((share/decimals)/market);
  nonshare        =  parseFloat(BinancePortcent) + parseFloat(paralelPorcent);
  otherMarkets    = m((((market - bin)-bin2) - (share/decimals))*decimals)
  datachart = {
 
    datasets: [
      {
        data: [BinancePortcent , paralelPorcent, 100-nonshare ],
        cutout:90,
        borderRadius: 100,
        borderSkipped: true,
        backgroundColor: [
          ' #f4c349',
          '#0c3ee3',
          ' #f8f8f8',

        ]
      },
    ],
  };

}
  
const datase = [
  { name: 'Binance', value: parseInt(BinancePortcent) },
  { name: 'Parallel Finance', value: parseInt(paralelPorcent) },
  { name: 'Others', value: 100-nonshare },
];
const COLORS = ['#f6cc27', '#0c3ee3', '#f8f8f8'];
  return (  
    <>
    <div className="dominance">
      <div className="chart-dominance">
        <div className="centered score-text">
            {m(data?.share.market)} <small>DOT</small>

        </div>
        <ResponsiveContainer width="100%" height={285}>
        <PieChart >
        <Pie
            data={datase}
            innerRadius={105}
            outerRadius={120}
            cornerRadius={50}
            paddingAngle={2}
            dataKey="value"
          >
            <Tooltip />
            {datase.map((entry, index) => (
              <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
            ))}
          </Pie>
        </PieChart>  
        </ResponsiveContainer>  
      </div>
      <div>
        <img className="logo" src={Binance} alt="Binance" />
        <p>Binance <strong>{!bin ? 'loading' : m((bin+bin2)*decimals) + ' ('+ BinancePortcent +'%)' }  </strong></p>
         
      </div>
      <div>
        <img className="logo" src={Parallel} alt="Binance" />
        <p>Parallel Finance <strong>{ !share ? 'getting' : m(share) + ' ('+ paralelPorcent +'%)'}</strong></p>
        
      </div>
      <div>
        <img className="logo" src={Networks} alt="Binance" />
        <p>Others <strong>{ !share ? 'getting' : otherMarkets + ' ('+ (100-nonshare).toFixed(2) +'%)'}</strong></p>
      </div>     
    </div>

    </>
  )
}


