import React, { useEffect, useState } from 'react';
import { n, Porcent } from './helpers.jsx';
import { BarChart, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import _ from "lodash"

export default function GlobalTvl(data) {
  let dotPrice = data.price.dotPrice;
  let filters    = data.dots;
  let ff         = filters.filter;
  let total      = filters.totalContributions;
  return (
    <>

      <p className="big">
        {n(total) + ' Contributions'}
      </p>

      <div className="flex column">
          {Object.keys(ff).map((keyName, i) => (
            <>
              <div className="flex">
                <div>
                  {(() => {
                    switch (keyName) {
                      case '100':
                        return <p><strong>&#60;100</strong></p>
                      case '1000':
                        return <p><strong>100 - 1k</strong></p>
                      case '10000':
                      return <p><strong>1k - 10k</strong></p>   
                      case '100000':
                      return <p><strong>10k - 100k</strong></p>                                        
                      default:
                        return null
                    }
                  })()}
                </div>
                <div>
                  <div className="travelcompany-input" key={i}>
                    <svg className="score small"  width="48" height="48" viewBox="-25 -25 400 400">
                      <circle className="score-empty" cx="175" cy="175" r="175" strokeWidth="25" fill="none"></circle>
                      <circle className="js-circle score-circle" transform="rotate(-90 175 175)" cx="175" cy="175" r="175" strokeDasharray="1100" 
                        strokeDashoffset={ (1100 * (100 - (100 - Porcent((total - ff[keyName].total )/total)).toFixed(2))) / 100} strokeWidth="25" fill="none"></circle>
                      <text className="js-text score-text big" x="50%" y="55%" dx="-25" textAnchor="middle">
                        {(100 - Porcent((total - ff[keyName].total )/total)).toFixed(0) + '%' }
                      </text>
                    </svg>                 
                  </div>
                </div>
                <div>
 
                  <div className="travelcompany-input" key={i}>
                    <span className="input-label">{n(ff[keyName].value)}</span>
                  </div>
                </div>
              </div>
            </>
          ))}
        </div>

    </>
  )
}


