import React from 'react';
import Parachains from '../../data/parachains.jsx';
import { n, m , Porcent } from '../../data/helpers.jsx';
import GlobalTvl from '../../data/globalTvl.jsx';
import GlobalDotTvl from '../../data/globalDotTvl.jsx';
import Htvl from '../../data/Htvl.jsx';
import Share from '../../data/share.jsx';
import DailyDot from '../../data/DailyDot';
import Logo from '../../components/Logo';
import ProgressBar from '../../components/ProgressBar';
import Dotmarket from '../../components/Dotmarket';
import MarketDominance from '../../components/MarketDominance'
import DotContributionsFilter from '../../data/DotGroups.jsx';
import _ from 'lodash';

export default function CrowdloanList(data) {

  const raising     = data?.raise
  const contrib     = data?.contributions
  const parachain   = Parachains.parachain;
  const tvl         = data?.tvl;
  const htvl        = data?.htvl;
  const DotAmount   = data?.dotAmount; 
  const marketshare = [{}];
  const binance     = data?.other;
  let finalBinance = '';
  if(binance) {
    finalBinance = _.merge(_.keyBy(data?.other[0].data, 'id'), _.keyBy(data?.other[1].data, 'id'));
  }

  if(raising && contrib) {
    const market = raising.reduce(function(sum, current) {
      return parseInt(sum) + parseInt(current.raised);
    }, 0);
    const share = contrib.reduce(function(sum, current) {
      return parseInt(sum) + parseInt(current.total);
  }, 0)
    marketshare.market = market;
    marketshare.share  = share;
    marketshare.porcent = Porcent(marketshare.share/marketshare.market);
  }  


  return (
    <>
        <div className="flex">
          <div className="box tvl">
              <h3> Total Value Locked </h3>
              {tvl &&
                <p className="giant">
                  ${ n(tvl.totalValue)}
                </p>
              }
              {htvl &&
                <Htvl data={htvl}/>
              }
              
          </div>

          <div className="box small" >
            <h3>Parallel Share of Total Market</h3>
            <Share market={marketshare} price={tvl}/>
          </div>  
        </div>

        <div className="box full ">
          <h3>Crowdloan by Projects</h3>
          <div className="selectors">
            <p>
              <span><i className="gray"/> Total DOT Raised </span>
              <span><i className="blue"/> Raised on Parallel </span>
              <span><i className="yellow"/> Raised on Binance </span>
            </p>
          </div>
          { !raising && !binance ? 'Loading...' : 
            raising.sort((a, b) => b.raised - a.raised).filter(data => data.paraId !== "2017" && data.paraId !== "2003" && data.paraId !== "2008").map((index) => {
              
              const id = index.paraId,
                    r  = index.raised,
                    t  = raising[0].raised,
                    b  = Object.values(finalBinance).filter(obj => {
                      return obj.code === index.paraId
                    });
                    const dataBinance = b[0] ? b[0].votedAmount*10000000000 : 0;

              return (

                <div  key={id} id={id}>
                  <div className="flex">
                    
                    {!parachain ? 'Loading...' : parachain.filter(i => i.paraid === id).map(icon => (
                      <Logo key={index.paraId} src={icon}/>
                    ))}    

                    {!contrib ? 'Loading...' : contrib.filter(g => g.paraId === id).map(graph => {
                      const gTotal = graph.total/r,
                            bgTotal = dataBinance/r,
                            rt     = r/t;
                      return (
                      <>
                        <div className="crowdloan-by">
                          <ProgressBar key={index.paraId} width={rt} bar={gTotal} bar2={bgTotal}/>
                          <div>
                            <p className="marketData">
                              <strong> 
                                { r ? m(r) : '0' } 
                              </strong> | <span> 
                                { Porcent(gTotal) + '% (' + m(graph.total) + ')' } 
                              </span>  
                            </p>
                          </div>
                        </div>
                      </>
                    )})}
                    
                  </div>
                </div>
                
              )
          } )
          }
        </div>

        <div className="flex">
          <div className="box tvl auctions" >
            <h3>DOT Auctions <span>Total Supply: <strong>1,103,303,471 DOT</strong></span></h3>
            <Dotmarket raise={raising} marketShare={marketshare.market}/>
          </div>  
          <div className="box small dominance-market">
            <h3>Market Dominance</h3>
            {binance &&
              <MarketDominance data={binance} share={marketshare}/>
            }
            </div> 
        </div>
        <div className="box full">
          <h3>Daily Increase of DOT raised during the crowdloan</h3>
          {htvl &&
            <DailyDot contributions={htvl}/>
          }
        </div>  
        <div className="box half ml-25">
          <h3>Crowdloan USD TVL </h3>
          {tvl && 
            <GlobalTvl price={tvl} />
          }          
        </div>
        <div className="box half mr-25">
          <h3>Crowdloan DOT TVL</h3>
          {tvl && 
            <GlobalDotTvl price={tvl} />
          }          
        </div>        
        {DotAmount && tvl && 
          <div className="box full distribution">
            <h3>Contribution Distribution</h3>
            
              <DotContributionsFilter dots={DotAmount} price={tvl}/>
          
          </div>   
        }
          
    </>
  );
}