import React from 'react';
import { m } from './helpers.jsx';

export default function Share(vals) {
  const price     = vals?.price?.dotPrice
  const share     = vals.market
  return (
    <>
        <svg className="score"  viewBox="-25 -25 400 400">
          <circle className="score-empty" cx="175" cy="175" r="175" strokeWidth="25" fill="none"></circle>
          <circle className="js-circle score-circle" transform="rotate(-90 175 175)" cx="175" cy="175" r="175" strokeDasharray="1100" 
            strokeDashoffset={ !share.market ? 0 : (1100 * (100 - share.porcent)) / 100} strokeWidth="25" fill="none"></circle>
          <text className="js-text score-text" x="50%" y="45%" dx="-25" textAnchor="middle">
            { !share.porcent ? 'calculating' : share.porcent + '%'}
          </text>
          <text className="js-text score-text small" x="50%" y="55%" dx="-25" textAnchor="middle">
          { !share.market  ? 'calculating' : 
            'of ' + m(share.market) + ' DOT'  
          }
          </text>
        </svg>  
    </>
  )
}