import React from 'react';

export default function ProgressBar(data) {
  const w = data.width,
        b = data.bar,
        c = data.bar2;
  return (
    <>
      <div className="meter" style={{width: 800*w }} >
        <span style={{width: b*100 + '%'}} data-tooltip={(b*100).toFixed(2) + '%'}></span>
        <span style={{left:b*100 + '%', width: parseInt((c*100)).toFixed(2)+ '%'}} data-tooltip={parseInt((c*100)).toFixed(2)+ '%'}></span>
      </div>
    </>
  )
}